<!-- 医废采集 -->
<template>
  <div>
    <Row :gutter="20">
      <Col :sm="6" :lg="4">
        <Input v-model="barCode" placeholder="条码"></Input>
      </Col>
      <Col :sm="6" :lg="4">
        <Select v-model="type">
          <Option
            v-for="item in typeList"
            :value="item.value"
            :key="item.value"
            :label="item.label"
          ></Option>
        </Select>
      </Col>
      <Col :sm="6" :lg="4">
        <Select v-model="state">
          <Option
            v-for="item in stateList"
            :value="item.value"
            :key="item.value"
            :label="item.label"
          ></Option>
        </Select>
      </Col>
      <Col :sm="6" :lg="4">
        <Date-picker type="daterange" format="yyyy/MM/dd" placeholder="选择日期和时间"></Date-picker>
      </Col>
      <Col :sm="6" :lg="4">
        <Button type="success">
          <Icon type="md-search" />查询
        </Button>
      </Col>
    </Row>
    <br />
    <Row>
      <Button type="error">
        <Icon type="md-close" />删除
      </Button>
    </Row>
    <br />
    <Card>
      <Table :columns="column1" :data="data1"></Table>
      <br />
      <Page :total="100" />
    </Card>
    <Modal v-model="infoModal" title="查看医废信息">
      <Form ref="formItem" :model="formItem" inline>
        <FormItem label="条码">
          <Input v-model="formItem.no" disabled></Input>
        </FormItem>
        <FormItem label="医废类型">
          <Select v-model="formItem.no">
            <Option v-for="item in list" :value="value" :key="item"></Option>
          </Select>
          <Input v-model="formItem.no"></Input>
        </FormItem>
        <FormItem label="重量">
          <Input v-model="formItem.no"></Input>
          <Input v-model="formItem.no"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "WasteCollection",
  data() {
    return {
      barCode: "",
      type: "1",
      state: "1",
      column1: [
        {
          key: "select",
          type: "selection",
          width: 60
        },
        {
          title: "批号"
        },
        {
          title: "条码",
          key: "barCode"
        },
        {
          title: "类型",
          key: "wasteType"
        },
        {
          title: "重量"
        },
        {
          title: "收集时间",
          key: "conllectionTime"
        },
        {
          title: "交接科室",
          key: "divisionName"
        },
        {
          title: "交接人",
          key: "hander"
        },
        {
          title: "收集人",
          key: "conllecter"
        },
        {
          title: "所属机构",
          key: "org"
        },
        {
          title: "状态",
          key: "state"
        },
        {
          title: "操作",
          render: h => {
            return h(
              "Button",
              {
                props: {
                  type: "success",
                  size: "small",
                  icon: "md-create"
                }
              },
              "查看"
            );
          }
        }
      ],
      data1: [],
      typeList: [
        {
          value: "1",
          label: "不限"
        },
        {
          value: "2",
          label: "感染性"
        },
        {
          value: "3",
          label: "损伤性"
        },
        {
          value: "4",
          label: "化学性"
        },
        {
          value: "5",
          label: "病理性"
        },
        {
          value: "6",
          label: "药物性"
        }
      ],
      stateList: [
        {
          value: "1",
          label: "不限"
        },
        {
          value: "2",
          label: "已称重"
        },
        {
          value: "3",
          label: "已提交"
        },
        {
          value: "4",
          label: "已复核"
        },
        {
          value: "5",
          label: "已采集复核"
        },
        {
          value: "6",
          label: "已装箱"
        }
      ],
      formItem: {
        no: ""
      }
    };
  }
};
</script>